
import React  from "react";
import { Card, CardImg, CardImgOverlay, CardTitle,CardBody,CardText, Breadcrumb,BreadcrumbItem } from "reactstrap";
import {Link} from 'react-router-dom'
 
 function RenderMenuItem({dish, onClick}){
     return(
        // <Card  onClick={() => onClick(dish.id)}> 
        <Card>
            <Link to={`/menu/${dish.id}`} >
            <CardImg width ="100%" object src ={dish.image} alt={dish.name} />
   
            <CardImgOverlay body className="ml-5">
            <CardTitle heading>{dish.name}</CardTitle>
      
             </CardImgOverlay>
             </Link>
        </Card>
     );
 }
    const Menu = (props) => {
        
        const menu = props.dishes.map((dish) => {
            return(
                    <div key = {dish.id} className="col-12 col-md-5 m-1">
                       {/* <RenderMenuItem dish={dish} onClick={props.onClick}/> */}
                       <RenderMenuItem dish={dish}/>
                    </div>
            );
        });
  
        
  
        return(
            <div className = "container">
                <div className="row">
                    <Breadcrumb>
                    <BreadcrumbItem><Link to='/home'>Home</Link>
                    </BreadcrumbItem>
                    <BreadcrumbItem active>LOB</BreadcrumbItem>
                    </Breadcrumb>
                    <div className="col-12">
                        <h3>Line of Business</h3>
                        <hr></hr>
                    </div>
                </div>
                <div className = "row">
                    
                        {menu}
  
                </div>
                <div className = "row">
                    {/* {this.renderDish(this.state.selectedDish)} */}
                </div>
            </div>
        );

    }
      
  



export default Menu;




